/* Initial input field (actually a UL styled to look like an input field) */
.exi-transitional ul.token-input-list {
    display: block;
    width: 100%;
    min-height: 2.5em;
    height: auto;
    padding: 0 0 .25em .25em;
    font-family: inherit;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.42857143;
    color: #333;
    background-color: #fff;
    border: 1px solid #d2d6db;
    border-radius: 4px;
    -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    -webkit-transition: border-color ease-in-out .15s,-webkit-box-shadow ease-in-out .15s;
    -o-transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
}

/* Input LI and selection chips */
.exi-transitional ul.token-input-list li {
    list-style-type: none;
    float: left;
}

/* Input field LI */
.exi-transitional ul.token-input-list li.token-input-input-token{
    margin: .25em .5em 0 0;
}

/* Actual input field inside LI */
.exi-transitional ul.token-input-list li.token-input-input-token input{
    border: none;
}

/* Selection chips */
.exi-transitional ul.token-input-list li.token-input-token {
    display: inline-block;
    overflow: hidden;
    width: auto;
    height: auto;
    margin: .25em .4em 0 0;
    padding: .4em .5em .25em .5em;
    background-color: #d7edff;
    color: #000;
    cursor: default;
    border: 1px solid #a1b6c6;
    font-size: 12px;
    font-weight: normal !important;
    border-radius: 4px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    white-space: nowrap;
}

/* Chip "remove" icon */
.exi-transitional li.token-input-token span {
    font-weight: 700;
    color: #337ab7;
    font-size: 1.1em;
    margin-left: .3em;
}

/* Selection options dropdown */
.exi-transitional div.token-input-dropdown {
    position: absolute;
    width: auto;
    max-width: 400px;
    overflow: hidden;
    color: #767676;
    padding: 0 .5em;
    border: 1px solid #d2d6db;
    border-top: none;
    border-radius: 0 0 4px 4px;
    cursor: default;
    font-size: 14px;
    font-weight: normal !important;
    font-family: inherit;
    z-index: 1;
}

/* Selection options dropdown highlighted search characters */
.exi-transitional div.token-input-dropdown b, .exi-transitional div.token-input-dropdown p {
    font-weight: normal;
}

/* Individual dropdown option items */
.exi-transitional div.token-input-dropdown ul li {
    margin: 0 -.5em;
    padding: .5em 1em;
    color: #333;
}

/* Background color of dropdown items (zebra-striped by default, now all white to match other dropdowns) */
.exi-transitional div.token-input-dropdown ul li.token-input-dropdown-item, .exi-transitional div.token-input-dropdown ul li.token-input-dropdown-item2 {
    background-color: #fff;
}

/* Hover color of dropdown items */
.exi-transitional div.token-input-dropdown ul li:hover {
    background: #f5f5f5;
}