.exi-transitional {

	label {
		margin-bottom: 0.5rem;
	}

	[type="radio"]:checked,
	[type="radio"]:not(:checked) {
		// move original radio buttons off screen
	    position: absolute;
	    left: -9999px;
	    opacity: 0;
	}

	[type="radio"]:checked + label,
	[type="radio"]:not(:checked) + label
	{
	    position: relative;
	    padding-left: 28px;
	    cursor: pointer;
	    line-height: 20px;
	    display: inline-block;
	    color: #666;
	}

	[type="radio"]:checked + label:before,
	[type="radio"]:not(:checked) + label:before {
	    content: '';
	    position: absolute;
	    left: 0;
	    top: 0;
	    width: 18px;
	    height: 18px;
	    border: 3px solid #444444;
	    border-radius: 100%;
	    background: #fff;
	}

	[type="radio"]:checked + label:after,
	[type="radio"]:not(:checked) + label:after {
	    content: '';
	    width: 12px;
	    height: 12px;
	    background: #3FA4EF;
	    position: absolute;
	    top: 3px;
	    left: 3px;
	    border-radius: 100%;
	    -webkit-transition: all 0.2s ease;
	    transition: all 0.2s ease;
	}

	[type="radio"]:not(:checked) + label:after {
	    opacity: 0;
	    -webkit-transform: scale(0);
	    transform: scale(0);
	}

	[type="radio"]:checked + label:after {
	    opacity: 1;
	    -webkit-transform: scale(1);
	    transform: scale(1);
	}

	// DISABLED STATE
	[type="radio"]:not(:checked)+label.disabled:before {
		background: #D3D3D3;
		border: 3px solid #BCBDBF;
	}

	// label.disabled {
	// 	&:hover {
	// 		cursor: auto;
	// 	}
	// }

	// RADIO GROUP 
	legend {
		font-weight: bold;
	}

}